body {
    background: #202124;
}

.editContainer {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    width: 300px;
    height: 250px;
}

.editorBanner {
    font-family: 'Ibarra Real Nova', serif;
    height: 25px;
    display: block;
    margin: auto;
    color: #ffffff;
    background-color: #2c2e30;
    width: 300px;
    border-radius: 5px;
    z-index: -1;
}

    .editorBanner span {
        position: relative;
        top: 3px;
    }

.editorText {
    background-color: #41434a;
    color: #ffffff;
    height: 250px;
    width: 300px;
}

textarea:focus {
    outline: none;
    border: 1px solid #a6a6a6;
    box-shadow: 0px 0px 3px #a6a6a6;
}

.previewBanner {
    position: relative;
    font-family: 'Ibarra Real Nova', serif;
    height: 25px;
    display: block;
    margin: auto;
    color: #ffffff;
    background-color: #2c2e30;
    width: 300px;
    border-radius: 5px;
    top: 10px;
    z-index: -1;
}

    .previewBanner span {
        top: 3px;
    }

.previewContainer {
    position: relative;
    top: 5px;
    margin: auto;
    overflow-y: auto;
    background-color: #41434a;
    color: #ffffff;
    width: 300px;
    height: 250px;
    border: solid black;
}

@media only screen and (min-width: 768px) {
    .editorBanner {
        width: 600px;
        font-size: 25px;
        height: 40px;
    }

    .editContainer {
        width: 600px;
        height: 500px;
    }

    .editorText {
        font-size: 20px;
        width: 600px;
        height: 500px;
    }

    .previewBanner {
        width: 600px;
        font-size: 25px;
        height: 40px;
    }

    .previewContainer {
        width: 600px;
        height: 500px;
        font-size: 20px;
    }
}

@media only screen and (min-width: 1400px) {
    .editorBanner {
        margin: 5px 0px 0px 5px;
        width: 700px;
    }

    .editContainer {
        margin: 5px 0px 0px 5px;
        width: 700px;
        height: 700px;
    }

    .editorText {
        font-size: 20px;
        width: 700px;
        height: 700px;
    }

    .previewBanner {
        position: relative;
        width: 700px;
        font-size: 25px;
        height: 40px;
    }

    .previewContainer {
        width: 700px;
        height: 715px;
        font-size: 20px;
    }

    .DisplayBoth {
        display: flex;
        justify-content: space-around;
    }
}

